/**
 * data: strict: 'strict'111
 * @param {(string | number)} data 
 * @param {string} Msg 
 * @param {'strict'} strict 111
 * @return {*}  {(string | false)} ()false()
 */
export function VPhone(
    data: string | number,
    Msg: string,
    strict: 'strict'
): string | false {
    let Str: any;

    if (data !== 0) {
        Str = data;
    } else {
        Str = '0';
    }

    Str ? (Str = Str.toString()) : null;

    const fixStrict = strict || null;

    if (!Str || Str.length !== 11) {
        return Msg || '11';
    }

    if (!/^[0-9]*$/.test(Str)) {
        return Msg || '';
    }

    if (fixStrict !== 'strict' && !/^1\d{10}$/.test(Str)) {
        return Msg || '111';
    }

    if (fixStrict === 'strict' && !/^1[3|4|5|7|8]\d{9}$/.test(Str)) {
        return Msg || '';
    }

    return false;
}

/**
 * validate the name
 * @export
 * @param {(string | number)} name
 * @param {string} Msg
 * @param {'Zh'} Zh
 * @return {*}  {(string | false)} ()false()
 */
export function VName(
    name: string | number,
    Msg: string,
    Zh: 'Zh'
): string | false {
    let Str: any;

    if (name !== 0) {
        Str = name;
    } else {
        Str = '0';
    }
    Str ? (Str = Str.toString()) : null;
    const fixZh = Zh || null;

    if (!Str || Str.length < 1) {
        return Msg || '';
    }

    if (fixZh !== 'Zh' && !/^[\u4E00-\u9FA5A-Za-z0-9]+$/.test(Str)) {
        return Msg || '';
    }

    if (fixZh === 'Zh' && Str.length < 2) {
        return Msg || '';
    }

    if (fixZh === 'Zh' && !/^[\u4e00-\u9fa5]+$/.test(Str)) {
        return Msg || '';
    }

    return false;
}

/**
 *
 * email
 * @export
 * @param {string} email
 * @param {string} Msg 
 * @return {*}  {(string | false)} ()false()
 */
export function VEmail(email: string, Msg: string): string | false {
    let Str: any;

    Str = email;

    Str ? (Str = Str.toString()) : null;

    if (!Str || Str.length < 1) {
        return Msg || '';
    }

    if (!/^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/.test(Str)) {
        return Msg || '';
    }

    return false;
}

/**
 *
 * 
 * @export
 * @param {(string| number)} data
 * @param {string} Msg
 * @return {*}  {(string | false)}
 */
export function VIdCard(data: string | number, Msg: string): string | false {
    let idcard: any;

    if (data !== 0) {
        idcard = data;
    } else {
        idcard = '0';
    }

    idcard ? (idcard = idcard.toString()) : null;

    const Errors = new Array(
        '!',
        '!',
        '!',
        '!',
        '!'
    );
    const area = {
        11: '',
        12: '',
        13: '',
        14: '',
        15: '',
        21: '',
        22: '',
        23: '',
        31: '',
        32: '',
        33: '',
        34: '',
        35: '',
        36: '',
        37: '',
        41: '',
        42: '',
        43: '',
        44: '',
        45: '',
        46: '',
        50: '',
        51: '',
        52: '',
        53: '',
        54: '',
        61: '',
        62: '',
        63: '',
        64: '',
        65: '',
        71: '',
        81: '',
        82: '',
        91: '',
    };
    let ereg, Y, JYM;
    let S, M;
    let idcard_array = new Array();
    idcard_array = idcard.split('');
    //
    if (area[parseInt(idcard.substr(0, 2), 0)] === null) {
        return Msg || Errors[4];
    }

    //
    switch (idcard.length) {
        case 15:
            if (
                (parseInt(idcard.substr(6, 2), 0) + 1900) % 4 === 0 ||
                ((parseInt(idcard.substr(6, 2), 0) + 1900) % 100 === 0 &&
                    (parseInt(idcard.substr(6, 2), 0) + 1900) % 4 === 0)
            ) {
                ereg = /^[1-9][0-9]{5}[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}$/; //
            } else {
                ereg = /^[1-9][0-9]{5}[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))[0-9]{3}$/; //
            }
            if (ereg.test(idcard)) {
                return false;
            }

            return Msg || Errors[2];

        case 18:
            //18
            //
            //:((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))
            //:((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))
            if (
                parseInt(idcard.substr(6, 4), 0) % 4 === 0 ||
                (parseInt(idcard.substr(6, 4), 0) % 100 === 0 &&
                    parseInt(idcard.substr(6, 4), 0) % 4 === 0)
            ) {
                ereg = /^[1-9][0-9]{5}(19|20)[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}[0-9Xx]$/; //
            } else {
                ereg = /^[1-9][0-9]{5}(19|20)[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))[0-9]{3}[0-9Xx]$/; //
            }

            if (ereg.test(idcard)) {
                //
                //
                S =
                    (parseInt(idcard_array[0], 0) +
                        parseInt(idcard_array[10], 0)) *
                        7 +
                    (parseInt(idcard_array[1], 0) +
                        parseInt(idcard_array[11], 0)) *
                        9 +
                    (parseInt(idcard_array[2], 0) +
                        parseInt(idcard_array[12], 0)) *
                        10 +
                    (parseInt(idcard_array[3], 0) +
                        parseInt(idcard_array[13], 0)) *
                        5 +
                    (parseInt(idcard_array[4], 0) +
                        parseInt(idcard_array[14], 0)) *
                        8 +
                    (parseInt(idcard_array[5], 0) +
                        parseInt(idcard_array[15], 0)) *
                        4 +
                    (parseInt(idcard_array[6], 0) +
                        parseInt(idcard_array[16], 0)) *
                        2 +
                    parseInt(idcard_array[7], 0) * 1 +
                    parseInt(idcard_array[8], 0) * 6 +
                    parseInt(idcard_array[9], 0) * 3;
                Y = S % 11;
                M = 'F';
                JYM = '10X98765432';
                M = JYM.substr(Y, 1); //
                if (M === idcard_array[17]) {
                    return false;
                }
                //ID
                return Msg || Errors[3];
            }
            return Msg || Errors[2];
        default:
            return Msg || Errors[1];
    }
}

/**
 * 
 * @export
 * @param {(string | number)} data 
 * @param {string} Msg
 * @param {number} length 
 * @return {*}  {(false | string)}
 */
export function VVerificationCode(
    data: string | number,
    Msg: string,
    length: number
): false | string {
    let Str;

    if (data !== 0) {
        Str = data;
    } else {
        Str = '0';
    }

    Str ? (Str = Str.toString()) : null;
    const fixLength = length || 4;

    if (length && isNaN(fixLength)) {
        return Msg || '';
    }

    if (!Str || Str.length !== fixLength) {
        return Msg || `${fixLength}`;
    }

    if (!/^[0-9]*$/.test(Str)) {
        return Msg || '';
    }

    return false;
}

/**
 * 
 * @export
 * @param {(string|number)} data
 * @param {string} Msg
 * @param {number} length
 * @return {*}  {(boolean|string)}
 */
export function VRequire(
    data: string | number,
    Msg: string,
    length: number
): boolean | string {
    let Str: any;

    if (data !== 0) {
        Str = data;
    } else {
        Str = '0';
    }

    Str ? (Str = Str.toString()) : null;
    const fixLength = length || 1;

    if (isNaN(fixLength) || !Str) {
        return Msg || '';
    }

    if (Str.length < fixLength) {
        return Msg || true;
    }

    return false;
}

/**
 * 
 * @export
 * @param {(string|number)} data
 * @param {string} Msg
 * @param {number} length
 * @return {*}  {(boolean | string)}
 */
export function VLimit(
    data: string | number,
    Msg: string,
    length: number
): boolean | string {
    let Str;

    if (data !== 0) {
        Str = data;
    } else {
        Str = '0';
    }

    Str ? (Str = Str.toString()) : null;
    const fixLength = length || 20;

    if (isNaN(fixLength) || !Str) {
        return Msg || '';
    }

    if (Str.length > fixLength) {
        return true;
    }

    return false;
}

