/**
 * 
 * @export
 * @param {string} dataA A
 * @param {string} dataB B
 * @param {string} Msg
 * @param {boolean} turnOver false 
 * @return {*}  {(boolean | string)}
 */
export function VEqual(
    dataA: string,
    dataB: string,
    Msg: string,
    turnOver: boolean
): boolean | string {
    if (!dataA || !dataB) {
        console.error('');
        return;
    }

    if (turnOver === false) {
        if (dataA !== dataB) {
            return Msg || '';
        }
    } else {
        if (dataA === dataB) {
            return Msg || '';
        }
    }

    return false;
}

/**
 * 
 * @export
 * @param {(string|number)} data
 * @param {string} Msg
 * @return {*}  {(boolean | string)}
 */
export function VDangerousChar(
    data: string | number,
    Msg: string
): boolean | string {
    let Str;

    if (data !== 0) {
        Str = data;
    } else {
        Str = '0';
    }

    Str ? (Str = Str.toString()) : null;

    if (
        /select |update |delete |truncate |join |union |exec |insert |drop |count||"|;|>|<|%/i.test(
            Str
        )
    ) {
        return Msg || true;
    }

    return false;
}


