/**
 * 
 * data
 * validate({
 *  VChinese_name: name,
 *  VChinese_address: address,
 * })
 * @param {({
 *         [keys: string]: string | any[];
 *     })} data keysvalue,
 * @param {boolean} isIncomplete
 * @return {*}  {*}
 */
function validate(
    data: {
        [keys: string]: string | any[];
    },
    isIncomplete?: boolean
): any {
    if (Object.prototype.toString.call(data) !== '[object Object]') {
        console.error('validate{key: value}');
        return '';
    }

    if (isIncomplete && typeof isIncomplete === 'boolean') {
        const VError = {};
        for (let key in data) {
            if ({}.hasOwnProperty.call(data, key)) {
                if (key.indexOf('_') !== -1) {
                    const splitkey = key.split('_')[0];
                    if (validate[splitkey]) {
                        if (Array.isArray(data[key])) {
                            const _Ad = validate[splitkey].apply(
                                validate[splitkey],
                                data[key]
                            );
                            if (_Ad) {
                                VError[key] = _Ad;
                            } else {
                                // VError[key] = false;
                            }
                        } else {
                            const _Nd = validate[splitkey](data[key]);
                            if (_Nd) {
                                VError[key] = _Nd;
                            } else {
                                // VError[key] = false;
                            }
                        }
                    }
                }

                if (key.indexOf('_') === -1) {
                    if (validate[key]) {
                        if (Array.isArray(data[key])) {
                            const Ad = validate[key].apply(
                                validate[key],
                                data[key]
                            );
                            if (Ad) {
                                VError[key] = Ad;
                            } else {
                                // VError[key] = false;
                            }
                        } else {
                            const Nd = validate[key](data[key]);
                            if (Nd) {
                                VError[key] = Nd;
                            } else {
                                // VError[key] = false;
                            }
                        }
                    }
                }
            }
        }

        if (Object.keys(VError).length === 0) {
            return false;
        }

        return VError;
    }

    for (let key in data) {
        if ({}.hasOwnProperty.call(data, key)) {
            if (key.indexOf('_') !== -1) {
                const splitkey = key.split('_')[0];
                if (validate[splitkey]) {
                    if (Array.isArray(data[key])) {
                        const _Ad = validate[splitkey].apply(
                            validate[splitkey],
                            data[key]
                        );
                        if (_Ad) {
                            return _Ad;
                        }
                    } else {
                        const _Nd = validate[splitkey](data[key]);
                        if (_Nd) {
                            return _Nd;
                        }
                    }
                }
            }

            if (key.indexOf('_') === -1) {
                if (validate[key]) {
                    if (Array.isArray(data[key])) {
                        const Ad = validate[key].apply(
                            validate[key],
                            data[key]
                        );
                        if (Ad) {
                            return Ad;
                        }
                    } else {
                        const Nd = validate[key](data[key]);
                        if (Nd) {
                            return Nd;
                        }
                    }
                }
            }
        }
    }

    return false;
}

